.card-image:hover {
	cursor: pointer;
}

.flex {
	display: flex;
	gap: 14px;
	align-items: center;

}

.card .card-content {
	padding: 14px;
}

.flex-vert {
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 20px;
}

.avatar {
	border-radius: 50%;
	height: 50px;
	width: 50px;
}